@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald Medium";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Oswald Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
html {
  height: 100%;
}

body {
  box-sizing: border-box;
  min-width: 320px;
  min-height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

ul {
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.promo {
  width: 100%;
  min-height: 480px;
}
@media (min-width: 600px) {
  .promo {
    min-height: 700px;
  }
}
@media (min-width: 768px) {
  .promo {
    min-height: 950px;
  }
}
@media (min-width: 1200px) {
  .promo {
    min-height: 696px;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: 50% 100%;
    background-position: 100% 0%;
  }
}

.container {
  min-width: 320px;
  height: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 60px;
  }
}

.btn {
  width: 280px;
  height: 40px;
  border: none;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #68B738;
  cursor: pointer;
  transition: 0.25s;
}
.btn:hover {
  background-color: #5EAA2F;
}

.header {
  width: 100%;
  height: 66px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    height: 109px;
  }
}
@media (min-width: 1200px) {
  .header {
    background-color: transparent;
  }
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 11px;
}
@media (min-width: 768px) {
  .header__inner {
    padding-top: 24px;
  }
}

.header__logo-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 191px;
}
@media (min-width: 768px) {
  .header__logo-group {
    width: 174px;
  }
}

.header__logo-img {
  width: 100%;
}

@media (min-width: 768px) {
  .header__logo-text {
    display: none;
  }
}

.navbar-wrapper {
  display: none;
}
@media (min-width: 768px) {
  .navbar-wrapper {
    display: block;
  }
}

.navbar__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 440px;
}

.navbar__link {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.25s all;
}
.navbar__link:hover {
  opacity: 0.6;
}
@media (min-width: 1200px) {
  .navbar__link {
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.intro {
  min-height: 480px;
  max-height: 700px;
  width: 100%;
  padding: 0;
}
@media (max-width: 400px) {
  .intro {
    background-image: url("../img/index-background-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-size: 100% 70%;
  }
}
@media (min-resolution: 2dppx) and (min-width: 320px) and (max-width: 400px) {
  .intro {
    background-image: url("../img/index-background-mobile@2x.jpg");
  }
}
.intro__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 27px;
}
@media (min-width: 400px) {
  .intro__inner {
    align-items: flex-start;
    padding-top: 40px;
  }
}
@media (min-width: 768px) {
  .intro__inner {
    padding-left: 87px;
  }
}
.intro__text {
  max-width: 396px;
  text-align: center;
  color: #ffffff;
}
@media (min-width: 400px) {
  .intro__text {
    text-align: left;
    color: #000000;
  }
}
@media (min-width: 500px) {
  .intro__text {
    max-width: 414px;
  }
}
.intro__title {
  padding-bottom: 40px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 500px) {
  .intro__title {
    font-size: 60px;
    line-height: 60px;
  }
}
.intro__subtitle {
  margin-bottom: 29px;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
}
@media (min-width: 500px) {
  .intro__subtitle {
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 401px) {
  .intro__img {
    position: absolute;
    top: 88%;
    left: 21%;
  }
}
@media (min-width: 500px) {
  .intro__img {
    top: 94%;
    left: 157px;
  }
}
@media (min-width: 600px) {
  .intro__img {
    top: 92%;
    left: 80px;
  }
}
@media (min-width: 768px) {
  .intro__img {
    top: 240px;
    left: -87px;
  }
}
@media (min-width: 1200px) {
  .intro__img {
    top: 45px;
    left: 30%;
  }
}
@media (min-width: 401px) and (max-width: 1200px) {
  .intro__btn {
    position: absolute;
    top: 181px;
    left: 0;
  }
}
@media (min-width: 400px) {
  .intro__btn {
    width: 200px;
    height: 44px;
  }
}
@media (min-width: 500px) {
  .intro__btn {
    top: 237px;
  }
}
@media (min-width: 768px) {
  .intro__btn {
    top: 267px;
    width: 244px;
    height: 54px;
  }
}

.programs {
  margin-top: 22px;
}
@media (min-width: 1200px) {
  .programs {
    margin-top: 82px;
  }
}
.programs__list {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.programs__list:not(:last-child) {
  margin-bottom: 20px;
}
@media (min-width: 800px) {
  .programs__list {
    flex-direction: row;
    justify-content: space-between;
  }
}
.programs__item {
  min-width: 280px;
  min-height: 266px;
  margin-bottom: 20px;
  padding: 20px;
  background-color: #F2F2F2;
}
@media (min-width: 800px) {
  .programs__item {
    width: calc(50% - 30px);
  }
}
@media (min-width: 1200px) {
  .programs__item {
    width: calc(50% - 50px);
  }
}
.programs__header {
  display: flex;
  align-items: center;
  margin-bottom: 22px;
}
.programs__header-title {
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
}
.programs__header-icon {
  margin-right: 10px;
}
.programs__description {
  font-family: "Arial";
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 1200px) {
  .programs__description {
    font-size: 16px;
    line-height: 24px;
  }
}
.programs__link {
  margin-right: 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .programs__link {
    font-size: 20px;
  }
}
@media (min-width: 768px) {
  .programs__footer {
    margin-top: 23px;
  }
}
.programs hr {
  border: 1px solid #D9D9D9;
  margin: 23px 0 15px 0;
}
@media (min-width: 768px) {
  .programs hr {
    display: none;
  }
}
.programs [data=icon-cat-slim] {
  width: 50px;
  height: 53px;
}
@media (min-width: 1200px) {
  .programs [data=icon-cat-slim] {
    width: 100px;
    height: 106px;
  }
}
.programs [data=icon-cat-pro] {
  width: 67px;
  height: 50px;
}
@media (min-width: 1200px) {
  .programs [data=icon-cat-pro] {
    width: 134px;
    height: 100px;
  }
}
.programs [data=icon-arrow] {
  cursor: pointer;
}

.advantages {
  margin-top: 27px;
  min-height: 380px;
}
.advantages__title {
  margin-bottom: 40px;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  text-align: left;
}
@media (min-width: 768px) {
  .advantages__title {
    margin-bottom: 32px;
  }
}
@media (min-width: 600px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
  }
}
@media (min-width: 768px) {
  .advantages__list {
    grid-template-columns: repeat(2, 245px);
    row-gap: 80px;
    column-gap: 145px;
  }
}
@media (min-width: 1200px) {
  .advantages__list {
    grid-template-columns: repeat(4, minmax(230px, 250px));
    column-gap: 64px;
    grid-auto-rows: minmax(280px, auto);
  }
}
.advantages__item {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.advantages__item::after {
  content: "";
  position: absolute;
  top: 45%;
  right: 0;
  z-index: -1;
  font-size: 280px;
  color: #F2F2F2;
}
@media (min-width: 768px) {
  .advantages__item {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 37px;
  }
}
.advantages__description {
  width: 100%;
  font-family: "Arial";
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 768px) {
  .advantages__description {
    font-size: 16px;
  }
}
.advantages__icon-group {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  min-width: 60px;
  min-height: 60px;
  background-color: #68B738;
}
@media (min-width: 768px) {
  .advantages__icon-group {
    min-width: 80px;
    min-height: 80px;
    margin-bottom: 31px;
  }
}

@media (min-width: 768px) {
  .advantages__item--one::after {
    content: "1";
  }
  .advantages__item--two::after {
    content: "2";
  }
  .advantages__item--three::after {
    content: "3";
  }
  .advantages__item--four::after {
    content: "4";
  }
}